const ultimoCursor = document.querySelector('.hero__titulo:last-child #cursor');
const animarTexto = (element) => {
	const numeroDeLetras = element.dataset.texto;
	const cursor = element.querySelector('#cursor');

	
	cursor.classList.add('hero__cursor--visible')
	
	for (let i = 0; i < numeroDeLetras.length; i++) {
		setTimeout(() => {
			const letras = numeroDeLetras[i];
			const letra = document.createElement('span');
			letra.append(letras);
			element.append(letra);			
		}, 100 * i);
	}
	
	const cursores = element.querySelector('#cursor');
	setTimeout(() => {
		cursores.style.display = 'none';
		ultimoCursor.style.display = 'block';
	}, numeroDeLetras.length * 100);
	
	
	return new Promise((resolve) => {
		ultimoCursor.classList.add('hero__cursor--active')
		setTimeout(resolve, numeroDeLetras.length * 100);
	});
}

export default animarTexto