const galeria = document.querySelector('#trabajos');

let lastScrollTop = 0;

const appearFunction = () => {
	const trabajos = galeria.querySelectorAll('.trabajos__imagenes a');
	trabajos.forEach((element, index) => {
		setTimeout(() => {
			element.classList.add('trabajos__trabajo--visible')					
		}, 300 * index);
	});
}

const observer = new IntersectionObserver(
	(entries) => {
		if(entries[0].isIntersecting){
			appearFunction();
		}
	}, {
		//se colocan los parametros en este segundo objeto
		rootMargin: '0px 0px 0px 0px',
		//threshold indica el porcentaje de aparicion
		threshold: 0.3,
	}	
);

observer.observe(galeria);

