import "./animarGaleria"
import "./galeria"
import "./slider"
import "./ventanaCorreo"
import animarTexto from "./animarTexto";
import Swiper from 'swiper/bundle';
import '../sass/index.scss';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'swiper/css/bundle';



window.addEventListener('load', async() => {
	await animarTexto(document.querySelector('.hero__titulo--uno'));
	await animarTexto(document.querySelector('.hero__titulo--dos'));
	await animarTexto(document.querySelector('.hero__titulo--tres'));

	const burbujas = document.querySelectorAll('.hero__burbuja');
	for (let i = 0; i < burbujas.length; i++) {
		burbujas[i].classList.add('hero__burbuja--active-' + parseFloat(i + 1));
	}


	const swiper = new Swiper('.swiper__detalles', {
		loop: true,
		autoplay: {
		delay: 4000, // Tiempo entre slides (en milisegundos)
		disableOnInteraction: false,
		},
		pagination: {
		el: '.swiper-pagination',
		clickable: true,
		},
	});

})
