const btnAbrirCorreo = document.querySelectorAll('[data-action="abrir-ventana-correo"]')
const ventanaCorreo = document.querySelector('#ventana-correo');

btnAbrirCorreo.forEach((boton) => {
    boton.addEventListener('click', (e) => {
        e.preventDefault();
        ventanaCorreo.classList.add('ventana--active');
    });    
});

ventanaCorreo.addEventListener('click', (e) => {
	if(e.target.matches('.ventana__overlay')) {
        ventanaCorreo.classList.remove('ventana--active');
	}
    if(e.target.closest('.ventana__btn-cerrar')) {
		ventanaCorreo.classList.remove('ventana--active');
	}
})